import React, { useEffect } from 'react'
import { Container, Row, Col, Card, Image } from 'react-bootstrap'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC'
import { GET_LOCAL } from '../../../../../utils'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'

const CMSBrands = [
    {
        title: 'Shopify',
        icon: 'shopify',
        fun: 'shopify'
    },
    {
        title: 'Google Merchant Center',
        icon: 'google',
        fun: 'google'
    },
    {
        title: 'BigCommerce',
        icon: 'bigcommerce',
        fun: 'bmc'
    },
    {
        title: 'Magento',
        icon: 'magento',
        fun: 'magento'
    },
    {
        title: 'Woocommerce',
        icon: 'woocommerce',
        fun: 'woocommerce'
    },
    {
        title: 'Manual Upload',
        icon: 'manual-upload',
        fun: ''
    }
]

export const SyncProducts = ({ openManualSyncModal, openSyncProduct }) => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    
    const checkTour = async () => {
        const tourStep = GET_LOCAL('tourStep');
        const isTourComplete = GET_LOCAL('isTour') ?? true;
        const { default: appTour } = await import('../../../../../actions/appTour')
        const { tour } = new appTour(navigate, false, dispatch);
        if (!isTourComplete && tourStep?.id == '#sync_products-container') setTimeout(() => tour.drive(tourStep?.step ?? 5), 200);
    }

    // useEffect(() => {
    //     checkTour();
    // }, [])

    return <Container>
    <div className='f-center w-100 sync_products' style={{ minHeight: 'calc(100vh - 150px)' }}>
        <Row className='justify-content-center pb-lg-10' id="sync_products-container">
            <Col lg={8} className='w-100'>
                <div className="mb-5 mb-lg-10">
                    <h3>Sync Product Catalog</h3>
                    <p className='fw-medium text-muted'>Select the application from which you want to tag products</p>
                </div>
                <Row className='gx-3 w-100'>
                    {CMSBrands?.length ? CMSBrands.map((Item, Index) => (
                        <Col xs={6} sm={4} key={Index}>
                            <Card id={`${Item.icon}_btn_`} className='card-stretch gutter-b cursor-pointer' onClick={() => Item.fun === '' ? openManualSyncModal() : openSyncProduct(Item.fun)}>
                                <Card.Body className='flex-column f-center px-2'>
                                    <Image className={`my-3 ${Item.fun === 'bmc' || Item.fun === '' ? 'invert' : ''}`} src={baseUrl(`media/images/cms/${Item.icon}.svg`)} height={40} width={40} alt={Item.title} fluid/>
                                    <h5 className='text-center fs-8' style={{ minHeight: 30 }}>{Item.title}</h5>
                                </Card.Body>
                            </Card>
                        </Col>
                    )) : null}

                    {/* Request New Platform Button */}
                    <Col xs={6} sm={4}>
                        <RequestButtonHOC title={'Request Platform'}>
                            <Card className='card-stretch gutter-b cursor-pointer'>
                                <Card.Body className='flex-column f-center px-2'>
                                    <div className="my-3 position-relative" style={{ width: 40, height: 40 }}>
                                        <div className="position-absolute w-100 h-100 rounded-circle bg-white" style={{ border: '1px dashed #D7D7D7' }}></div>
                                        <div className="position-absolute w-100 h-100 rounded-circle" style={{ background: '#EBEBEB', transform: 'scale(0.85)' }}></div>
                                        <div className="position-absolute w-100 h-100 d-flex align-items-center justify-content-center">
                                            <FontAwesomeIcon icon={'plus'} iconClass="fs-5" />
                                        </div>
                                    </div>
                                    <h5 className='text-center fs-8' style={{ minHeight: 30 }}>Request Platform</h5>
                                </Card.Body>
                            </Card>
                        </RequestButtonHOC>
                    </Col>
                </Row>
            </Col>
        </Row>
    </div>
</Container>
}